@import url(https://fonts.googleapis.com/css?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css?family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.cellClosed {
  width: 40px;
  height: 40px;
  border-top: solid 3px #f2f2f2;
  border-left: solid 3px #f2f2f2;
  border-right: solid 3px #737373;
  border-bottom: solid 3px #737373;
  background-color: #d3d3d3;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center; }

.cellOpened {
  font-family: 'VT323', monospace;
  font-weight: bold;
  font-size: 30px;
  width: 40px;
  height: 40px;
  border: solid 1px #737373;
  background-color: #d3d3d3;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center; }

.bombImg {
  width: 30px;
  height: 30px; }

.bomb {
  background-color: red; }

.board {
  display: flex;
  margin: 20px;
  border-top: solid 5px #737373;
  border-left: solid 5px #737373;
  border-right: solid 5px #f2f2f2;
  border-bottom: solid 5px #f2f2f2; }

.gameView {
  background-color: #d3d3d3;
  padding: 10px;
  display: inline-flex;
  flex-direction: column;
  margin: auto; }

.headBox {
  height: 80px;
  border-top: solid 5px #737373;
  border-left: solid 5px #737373;
  border-right: solid 5px #f2f2f2;
  border-bottom: solid 5px #f2f2f2;
  display: flex;
  align-items: center;
  font-family: 'VT323', monospace;
  color: red; }

.score {
  background-color: black;
  font-size: 30px;
  margin: 10px;
  border-top: solid 5px #737373;
  border-left: solid 5px #737373;
  border-right: solid 5px #f2f2f2;
  border-bottom: solid 5px #f2f2f2; }

.avatar {
  width: 30px;
  height: 30px;
  margin: auto;
  border-top: solid 5px #f2f2f2;
  border-left: solid 5px #f2f2f2;
  border-right: solid 5px #737373;
  border-bottom: solid 5px #737373; }

.timer {
  background-color: black;
  font-size: 30px;
  margin: 10px;
  border-top: solid 5px #737373;
  border-left: solid 5px #737373;
  border-right: solid 5px #f2f2f2;
  border-bottom: solid 5px #f2f2f2; }

.home {
  border: 4px solid red; }

